/* CSS styles */
.profilebody {
  width: 100%;
  margin: 50px;
}
.profilimg {
  height: 200px;
  width: 200px;
  background-size: cover;
  background-image: url("https://png.pngitem.com/pimgs/s/532-5320961_stickers-i-usually-use-will-be-uploaded-on.png");
  margin: 10px;
  object-fit: cover;
  border-radius: 50%;
  filter: grayscale(55%);
}
.profilimg:hover {
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  transition: 0.3s;
  text-decoration: none;
  filter: grayscale(0%);
}
.profilimg:not(:hover) {
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  transition: 0.3s;
  text-decoration: none;
  filter: grayscale(55%);
}
.username {
  font-size: 58px;
  font-family: Impact;
  color: #495251;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 4px;
}
.profileheader {
  font-size: 18px;
  font-family: Impact;
  color: #495251;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.profilbox {
  width: 800px;
  height: auto;
  background-color: #99e1d9;
  border-radius: 0px 4px 4px 0px;
  padding: 60px;
  box-shadow: 20px 20px teal;
}
.profilbox:hover {
  box-shadow: 25px 25px #014848;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  transition: 0.3s;
  text-decoration: none;
}
.profildetail {
  display: block;
  color: #495251;
  font-family: Calibri;
  overflow: auto;
  margin: 0px 10px 0px 10px;
  padding: 0px 10px 0px 10px;
  height: 190px;
  width: 200px;
}
.profildetailuser {
  font-size: 12px;
  font-family: Calibri;
  color: #495251;
}
.profiltext {
  margin-top: 5px;
  margin-left: 10px;
  color: #495251;
  font-family: Calibri;
}
.igimg {
  background-size: cover;
  background-image: url("https://cdn-icons-png.flaticon.com/128/1384/1384015.png");
  width: 35px;
  height: 35px;
  margin-right: 10px;
  margin-top: -15px;
}
.fbimg {
  background-size: cover;
  background-image: url("https://logodix.com/logo/659068.png");
  width: 45px;
  height: 45px;
  margin-right: 80px;
  margin-top: -15px;
}
.friend {
  background-size: cover;
  background-image: url("https://static.thenounproject.com/png/2235846-200.png");
  width: 45px;
  height: 45px;
  margin-bottom: 15px;
}
